import React from "react";

//Importando la vista que utilizamos para los estilos
import "../css/Vista_Consulta_Usuarios.css";

const state = {
  i:[],
  nombre:"",
  apellido:"",
}

//Funcion para traer los datos de respuesta por parte de fetch
function VistaUsuariosInactivos (props) { 

  // Traer los datos de JSON dependiendo su id
  const mostrarOcultar = (i) => {

    // Consultar si los datos se estan trayendo
    if(i){
  
      state.vmodal = [i.url_foto];
      console.log(state.vmodal);
  
      //Condicional para el manejo de rol 
      //de numeracion a texto
  
      if(i.id_rol == "1")
      {
        let rol= document.getElementById('rol')
        rol.innerHTML = `
        Desarrollador
        `
      }else if (i.id_rol == "2"){
        let rol= document.getElementById('rol')
        rol.innerHTML = `
        Tematico
        `
      }else if(i.id_rol == "3"){
        let rol= document.getElementById('rol')
        rol.innerHTML = `
        Multimedia
        `
      }else{
        let rol= document.getElementById('rol')
        rol.innerHTML = `
        Coordinador
        `
      }
  
  
      let imagenmodal = document.getElementById('imagenmodal')
      imagenmodal.innerHTML = `
      <img id="imagenmodal" src=${i.url_foto} alt=""></img>
      `
      // Retorno de los datos para cada uno de los usuarios
  
      let datospersona = document.getElementById('datospersona')
      datospersona.innerHTML = `
      ${i.nombres}
      `
      let numerotajadas = document.getElementById('numerotajadas')
      numerotajadas.innerHTML = `
      ${i.tajadas}
      `
      let informacionpersona = document.getElementById('informacionpersona')
      informacionpersona.innerHTML = `
      ${i.profesion}
      `
      let fechaentrada = document.getElementById('fechaentrada')
      fechaentrada.innerHTML = `
      ${i.fecha_entrada}
      `
      let fechanacimiento = document.getElementById('fechanacimiento')
      fechanacimiento.innerHTML = `
      ${i.fecha_nacimiento}
      `
      let gustos = document.getElementById('gustos')
      gustos.innerHTML = `
      ${i.gustos}
      `
      let correopersonal = document.getElementById('correopersonal')
      correopersonal.innerHTML = `
      ${i.correo_personal}
      `
      let correocorporativo = document.getElementById('correocorporativo')
      correocorporativo.innerHTML = `
      ${i.correo_corporativo}
      `
      let telefono = document.getElementById('telefono')
      telefono.innerHTML = `
      ${i.telefono}
      `
    }
    if(i.url_foto == null)
    {
      let imagenmodal = document.getElementById('imagenmodal')
      imagenmodal.innerHTML = `
      Espacio vacio  
      `
    }
    if(i.nombres == null)
    {
      let datospersona = document.getElementById('datospersona')
      datospersona.innerHTML = `
      Espacio vacio  
      `
    }
    if(i.tajadas == null)
    {
      let numerotajadas = document.getElementById('numerotajadas')
      numerotajadas.innerHTML = `
      Espacio vacio  
      `
    }
    if(i.profesion == null)
    {
      let informacionpersona = document.getElementById('informacionpersona')
      informacionpersona.innerHTML = `
      Espacio vacio  
      `
    }
    if(i.fecha_entrada == null)
    {
      let fechaentrada = document.getElementById('fechaentrada')
      fechaentrada.innerHTML = `
      Espacio vacio  
      `
    }
    if(i.fecha_nacimiento == null)
    {
      let fechanacimiento = document.getElementById('fechanacimiento')
      fechanacimiento.innerHTML = `
      Espacio vacio  
      `
    }
    if(i.gustos == null)
    {
      let gustos = document.getElementById('gustos')
      gustos.innerHTML = `
      Espacio vacio  
      `
    }
    if(i.correo_personal == null)
    {
      let correopersonal = document.getElementById('correopersonal')
      correopersonal.innerHTML = `
      Espacio vacio  
      `
    }
    if(i.correo_corporativo == null)
    {
      let correocorporativo= document.getElementById('correocorporativo')
      correocorporativo.innerHTML = `
      Espacio vacio  
      `
    }
    if(i.telefono == null)
    {
      let telefono = document.getElementById('telefono')
      telefono.innerHTML = `
      Espacio vacio  
      `
    }
  
    
  }
  
return(
  <React.Fragment>
  <div className="titulo">
  <br></br>
  <br></br>
    <div className="separator">
      &nbsp;&nbsp;Usuarios Inactivos&nbsp;&nbsp;
    </div>
  </div>
  <div id="containestarjetas">
    {
          props.usuariosrespuestaInactivos?.map((item, i) => {
            state.nombre = item.nombres.split(" ");
            state.apellido = item.apellidos.split(" ");
           
            if(item.id_rol == '3'){
            return(
              <div key={i}>          
            <div id="crd" className="box-cards" onClick={(i) => mostrarOcultar (item, i)}>
              <div id="cambio" className="box box1" data-toggle="modal" data-target="#modalDatos">
                <img id="imagen" src={(item.url_foto)} alt=""></img>
                <span className="botton botton1">{ state.nombre[0] + " " + state.apellido[0] }</span>
              </div>
              </div>
            </div> 
              ) 
            }
            else if(item.id_rol == '1'){
              return(
                <div key={i}>            
                  <div id="crd" className="box-cards" onClick={(i) => mostrarOcultar (item, i)}>
                    <div id="cambio" className="box box2" data-toggle="modal" data-target="#modalDatos">
                      <img id="imagen" src={(item.url_foto)} alt=""></img>
                      <span className="botton botton2">{ state.nombre[0] + " " + state.apellido[0] }</span>
                    </div>
                  </div>
                </div> 
                ) 
            }else if (item.id_rol =='2'){
              return(
                <div key={i}>       
                  <div id="crd" className="box-cards" onClick={(i) => mostrarOcultar (item, i)}>
                    <div id="cambio" className="box box3" data-toggle="modal" data-target="#modalDatos">
                      <img id="imagen" src={(item.url_foto)} alt=""></img>
                      <span className="botton botton3">{ state.nombre[0] + " " + state.apellido[0] }</span>
                    </div>
                  </div>
                </div> 
                ) 
            }else if (item.id_rol == '4'){
              return(
                <div key={i}>            
                  <div id="crd" className="box-cards" onClick={(i) => mostrarOcultar (item, i)}>
                    <div id="cambio" className="box box4" data-toggle="modal" data-target="#modalDatos">
                      <img id="imagen" src={(item.url_foto)} alt=""></img>
                      <span className="botton botton4">{ state.nombre[0] + " " + state.apellido[0] }</span>
                    </div>
                  </div>
                </div> 
                ) 
              }
            else{ 
              return(
                <div key={i}>            
                  <div id="crd" className="box-cards" onClick={(i) => mostrarOcultar (item, i)}>
                    <div id="cambio" className="box box4" data-toggle="modal" data-target="#modalDatos">
                      <img id="imagen" src={(item.url_foto)} alt=""></img>
                      {
                        state.nombre = item.nombres.split(" "),
                        state.apellido = item.apellidos.split(" "),
                        console.log(state.nombre),
                        console.log(state.apellido)
                      }
                      <span className="botton botton4">{ state.nombre[0] + " " + state.apellido[0] }</span>
                    </div>
                  </div>
                </div> 
                )
            }
      
          }
            
          )
    }
    </div>

    <div className="modal fade" id="modalDatos" tabindex="-1" role="dialog" aria-labelledby="exampleModalLabel">
        <div className="modal-dialog" role="document">
          <div className="modal-content contenido">
            <div className="page-content page-container" id="page-content">
              <div className="justify-content-center">
                <div>
                  <div className="card mod user-card-full">
                    <div className="row row2 m-l-0 m-r-0">

                        <div className="col-sm-4  bg-c-lite-green row3">
                        <div className="card-block text-white justify-content-center top-20%">
                        <div id="row4">
                          <div className="m-b-25"> 
                          <div className="card-img-top imagen" id="imagenmodal"></div>
                          </div>
                          </div>
                          
                            <div>
                              <h6 id="datospersona"className="f-w-600"></h6>
                            </div>
                                <br />
                                  <p>Tajadas</p>
                                    <div id="numerotajadas">
                                    </div>
                                    </div>
                      </div>
                        <div className="col-sm-8">

                            <div className="prueba">
                                <br/> 
                                <h5>Información Personal</h5>
                            <table class="table table-hover  table-sm">
                                        <tbody className="h6">
                                            <tr>
                                            <th scope="col" >Fec. Nacimiento:</th>
                                            <td id="fechanacimiento">App nombre</td>
                                            </tr>
                                            <tr>
                                            <th scope="row">Gustos</th>
                                            <td id="gustos">Ejemplo</td>
                                            </tr>
                                            </tbody>
                            </table>
                            <h5>Contacto</h5>
                            <table class="table table-hover  table-sm">
                                        <tbody className="h6">
                                            <tr>
                                            <th scope="col">Telefono:</th>
                                            <td id="telefono">App nombre</td>
                                            </tr>
                                            <tr>
                                            <th scope="row">Correo personal:</th>
                                            <td id="correopersonal">Cumplir con las necesidades del usuario</td>
                                            </tr>
                                            <tr>
                                            <th scope="row">Correo corporativo:</th>
                                            <td id="correocorporativo">6/11/2020</td>
                                            </tr>
                                            </tbody>
                            </table>
                            <h5>Información General</h5>
                            <table class="table table-hover table-sm">
                                        <tbody className="h6">
                                            <tr>
                                            <th scope="col">Profesión:</th>
                                            <td  id="informacionpersona">App nombre</td>
                                            </tr>
                                            <tr>
                                            <th scope="row">Rol:</th>
                                            <td id="rol">Cumplir</td>
                                            </tr>
                                            <tr>
                                            <th scope="row">Fec. inicio:</th>
                                            <td id="fechaentrada">6/11/2020</td>
                                            </tr>
                                            </tbody>
                            </table>                 
                            </div>
                                 
                            <div/>

                        </div>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>  
    </React.Fragment>                
    );
}

export default VistaUsuariosInactivos;