import React from "react";
import "../css/Registrar_Usuario.css";
import "bootstrap/dist/css/bootstrap.min.css";
import "bootstrap/dist/js/bootstrap.bundle.min.js"
import  DASH from "./Dashboard";
import sweet from "sweetalert";

class REGISTRARU extends React.Component {
    state = {
        res: []
    };


    ID = React.createRef();
    Nombres = React.createRef();
    Apellidos = React.createRef();
    Fecha_nacimiento = React.createRef();
    Telefono = React.createRef();
    Gustos = React.createRef();
    Profesion = React.createRef();
    Correo_corporativo = React.createRef();
    Fecha_salida = React.createRef();
    Fecha_entrada = React.createRef();
    Id_rol = React.createRef();
    Correo_personal = React.createRef();
crearusuario(){
    const id =this.ID.current.value;
    const nombres =this.Nombres.current.value;
    const apellidos =this.Apellidos.current.value;
    const fecha_nacimiento =this.Fecha_nacimiento.current.value;
    const telefono =this.Telefono.current.value;
    const gustos =this.Gustos.current.value;
    const profesion =this.Profesion.current.value;
    const correo_corporativo =this.Correo_corporativo.current.value;
    const  fecha_salida =this.Fecha_salida.current.value;
    const fecha_entrada =this.Fecha_entrada.current.value;
    const id_rol =this.Id_rol.current.value;
    const correo_personal =this.Correo_personal.current.value;

    const datos = {
        id_usuario:  id,
        nombres: nombres,
        apellidos: apellidos,
        fecha_nacimiento: "2020-06-15",
        telefono: telefono,
        gustos: gustos,
        profesion:  profesion,
        correo_corporativo:  correo_corporativo,
        contrasena: "EsriCol20202",
        fecha_salida:  "2020-06-15",
        fecha_entrada: "2020-06-15",
        id_rol: "1",
        id_estado: "1",
        id_horario: "4",
        correo_personal: correo_personal,
        hdv: null,
        foto: null,
        url_hdv: "1",
        url_foto: "https://i.imgur.com/Ejlu7Of.png",
        color_dash:"azul",
        tajadas:"0"
    }

    const envia = {
        method: 'POST',
        body: JSON.stringify(datos),
        headers: new Headers({
            'Content-Type': 'application/json',
            'Origin': 'http://semillerows3/PlataformaSemilleroBackend/api/',
            'Accept': 'application/json',
        }),
    };
    fetch('http://semillerows3/PlataformaSemilleroBackend/api/usuarios', envia)
        .then(response => {
            if (response.ok) {
                sweet({
                    text: "Usuario Creado",
                    icon: "success",
                    timer: "2500",
                    button: false
                  });
                return response.json()
            }
            
            sweet({
                text: "Usuario no creado",
                icon: "error",
                timer: "2500",
                button: false
              });
        })
        
        
    }
     
  
    render() {
        return (
            <>
            <DASH/>
            <div id="contenidogeneral">
                <div className="body4">
                    <div className="container">
                        <center>
                            <div className="card" id="s1tyle">
                                <br />
                                <h5 className="card-title" id="s3tyle=">Crear Usuario</h5>
                                <img src="https://i.imgur.com/Ejlu7Of.png" className="card-img-top" id="s2tyle" alt="..."></img>
                                <div className="card-body">


                                    <form id="s4tyle"  onSubmit={this.crearusuario}>
                                        <div className="form-row">
                                            <div className="form-group col-md-6">
                                                <label for="inputEmail4">Nombres</label>
                                                <input ref={this.Nombres} type="text" className="form-control" id="inputEmail4"></input>
                                            </div>
                                            <div className="form-group col-md-6">
                                                <label for="inputPassword4">Apellidos</label>
                                                <input ref={this.Apellidos} type="text" className="form-control" id="inputEmail4"></input>
                                            </div>
                                        </div>
                                        <div className="form-row">
                                            <div className="form-group col-md-6">
                                                <label for="inputEmail4">Profesión</label>
                                                <input ref={this.Profesion} type="text" className="form-control" id="inputEmail4"></input>
                                            </div>
                                            <div className="form-group col-md-6">
                                                <label for="inputPassword4">Fecha nacimiento</label>
                                                <input ref={this.Fecha_nacimiento} type="date" className="form-control" id="inputEmail4"></input>
                                            </div>
                                        </div>
                                        <div className="form-row">
                                            <div className="form-group col-md-6">
                                                <label for="inputEmail4">Correo personal</label>
                                                <input ref={this.Correo_personal} type="email" className="form-control" id="inputEmail4"></input>
                                            </div>
                                            <div className="form-group col-md-6">
                                                <label for="inputPassword4">Gustos</label>
                                                <input ref={this.Gustos} type="text" className="form-control" id="inputEmail4"></input>
                                            </div>
                                        </div>
                                        <div className="form-row">
                                            <div className="form-group col-md-6">
                                                <label for="inputEmail4">Fecha entrada</label>
                                                <input ref={this.Fecha_entrada} type="date" className="form-control" id="inputEmail4"></input>
                                            </div>
                                            <div className="form-group col-md-6">
                                                <label for="inputPassword4">Fecha salida</label>
                                                <input ref={this.Fecha_salida} type="date" className="form-control" id="inputEmail4"></input>
                                            </div>
                                        </div>
                                        <div className="form-row">
                                            <div className="form-group col-md-6">
                                                <label for="inputEmail4">Correo corporativo</label>
                                                <input ref={this.Correo_corporativo} type="email" className="form-control" id="inputEmail4"></input>
                                            </div>
                                            <div className="form-group col-md-6">
                                                <label for="inputPassword4">Cedula</label>
                                                <input ref={this.ID}  type="text" className="form-control" id="inputEmail4"></input>
                                            </div>
                                        </div>
                                        <div className="form-row">
                                            <div className="form-group col-md-12">
                                                <label for="inputState">Rol</label>
                                                <select ref={this.Id_rol} id="inputState" className="form-control">
                                                    <option selected>Desarrollador</option>
                                                    <option value="1">Tematico</option>
                                                    <option value="2">Multimedia</option>
                                                    <option value="3">Coordinador</option>
                                                </select>
                                            </div>
                                            <div className="form-group col-md-6">
                                                <label for="inputPassword4">Telefono</label>
                                                <input ref={this.Telefono} type="text" className="form-control" id="inputEmail4"></input>
                                            </div>
                                        </div>
                                        
                                    </form>
                                    <button type="submin" onClick={() => this.crearusuario()} className="btn btn-primary">Registrar</button>
                                      
                                </div>
                            </div>
                        </center>
                    </div>

                </div>
           </div>
           </>
        );
    }
}
export default REGISTRARU;