import React from "react";
import "../css/Perfil_Usuario.css";
import  DASH from "./Dashboard"
import NAV from "./nav";
import sweet from "sweetalert";
import Cookies from 'universal-cookie';
import moment from 'moment';
import axios from 'axios';

class PERFILU extends React.Component {

    componentDidMount(){
      

        
        const cookies = new Cookies();
        if(cookies.get('tajadas') =="0"){
        document.getElementById("lastajdas").classList.add("lastajdas1"); 
        }
    if(cookies.get('color_dash') =="azul"){
        document.getElementById("colordeelmodal").classList.add("colorazul"); 
        
    }else if(cookies.get('color_dash') =="morado"){
        document.getElementById("colordeelmodal").classList.add("colormorado"); 

    }else if(cookies.get('color_dash') =="verde"){
        document.getElementById("colordeelmodal").classList.add("colorverde");  

    }else if(cookies.get('color_dash') =="gris"){
        document.getElementById("colordeelmodal").classList.add("colorgris");  
        
    }
 
}

    escondeemos(){
        
    document.getElementById("idimout").classList.add("camhioacti"); 
    alert("hola mundo")

        
   
    }

    rotamos(){
        
           document.getElementById("rotar").classList.add("targetarotar");
            
        } 
        Imagen = React.createRef();
        Nombre = React.createRef();
        Apellido = React.createRef();
        TEL = React.createRef();
        CONTRASENA = React.createRef();
        CORREOP = React.createRef();
        ContrasenaActual = React.createRef();
        NuevaContrasena = React.createRef();
        ConfirmarContrasena = React.createRef();
        Profesion = React.createRef();
        Fecha_nacimiento = React.createRef();
        Gustos = React.createRef();
        Fecha_entrada = React.createRef();
        Fecha_salida = React.createRef();
        Correo_corporativo= React.createRef();
        ID = React.createRef();

        state={
            selectedFile: null
        }
        fileSelectedHadler = event =>{
            this.setState({
                selectedFile: event.target.files[0]
            })
            
        }

        fileUploadHadler= () =>{
        const fd = new FormData();
        const cookies = new Cookies();
        fd.append('image',this.state.selectedFile )
        axios.post('http://semillerows3/PlataformaSemilleroBackend/api/usuarios/simple',fd)
            .then((res) => {
              const nuestra =res.data.url

            const url_foto= cookies.get('url_foto');
            cookies.remove("url_foto",`${url_foto}`,{path: '/ ; '});

            const url_fotoactu= nuestra;
            cookies.set("url_foto",`${url_fotoactu}`, {path: '/ ; '} );
            window.location.href="/Perfil_Usuario";
        })
        
        }



        actualizarcontrasena=() =>{
            const antigua = this.ContrasenaActual.current.value;
            const nueva = this.NuevaContrasena.current.value;
            const confirmar  = this.ConfirmarContrasena.current.value;
            const cookies = new Cookies();
            

            if(antigua == cookies.get('contrasena')){
               if(nueva == confirmar){
                const contrasena= cookies.get('contrasena');
            cookies.remove("contrasena",`${contrasena}`,{path: '/ ; '});

            const contrasenanu=nueva;
            cookies.set("contrasena",`${contrasenanu}`, {path: '/ ; '} );
            sweet({
                text:'Contraseña Actualizada',
                icon: "success",
                timer: "2500",
                button: false
              });
        }else{
            sweet({
                text:'su contraseña no coinside  vuelva a intentarlo',
                icon: "error",
                timer: "2500",
                button: false
              });
        }
  
                        }else{
                        sweet({
                            text:'su contraseña es incorrecta vuelva a intentarlo',
                            icon: "error",
                            // timer: "2500",
                            button: true
                          });
                        

            }
        }

        

        actualizarusuario = () => {
    
            const nombre = this.Nombre.current.value;
            const apellido = this.Apellido.current.value;
            const numero = this.TEL.current.value;
            const correop = this.CORREOP.current.value;
            const profesion1 = this.Profesion.current.value;
            const fecha_nacimiento = this.Fecha_nacimiento.current.value;
            const gustos1 = this.Gustos.current.value;
            const fecha_entrada = this.Fecha_entrada.current.value;
            const fecha_salida1 = this.Fecha_salida.current.value;
            const correo_corporativo1 = this.Correo_corporativo.current.value;
            const id1 = this.ID.current.value;
            const cookies = new Cookies();
            

            const nombres= cookies.get('nombres');
            cookies.remove("nombres",`${nombres}`,{path: '/ ; '});
            const apellidos= cookies.get('apellidos');
            cookies.remove("apellidos",`${apellidos}`,{path: '/ ; '});
            const telefono= cookies.get('telefono');
            cookies.remove("telefono",`${telefono}`,{path: '/ ; '});
            
            const correo_personal= cookies.get('correo_personal');
            cookies.remove("correo_personal",`${correo_personal}`,{path: '/ ; '});
            const profesion= cookies.get('profesion');
            cookies.remove("profesion",`${profesion}`,{path: '/ ; '});
            const gustos= cookies.get('gustos');
            cookies.remove("gustos",`${gustos}`,{path: '/ ; '});
            const correo_corporativo= cookies.get('correo_corporativo');
            cookies.remove("correo_corporativo",`${correo_corporativo}`,{path: '/ ; '});
            const id= cookies.get('id');
            cookies.remove("id",`${id}`,{path: '/ ; '});

            const nombreactu=nombre;
            cookies.set("nombres",`${nombreactu}`, {path: '/ ; '} );
            const apellidoactu=apellido;
            cookies.set("apellidos",`${apellidoactu}`, {path: '/ ; '} );
            const telefonoactu=numero;
            cookies.set("telefono",`${telefonoactu}`, {path: '/ ; '} );
          
            const correopactu=correop;
            cookies.set("correo_personal",`${correopactu}`, {path: '/ ; '} );
            const profesionactu=profesion1;
            cookies.set("profesion",`${profesionactu}`, {path: '/ ; '} );
            const gustosactu=gustos1;
            cookies.set("gustos",`${gustosactu}`, {path: '/ ; '} );
            const correo_corporativoactu=correo_corporativo1;
            cookies.set("correo_corporativo",`${correo_corporativoactu}`, {path: '/ ; '} );
            const idactu=id1;
            cookies.set("id",`${idactu}`, {path: '/ ; '} );

            window.location.href="/Principal"; 
        
        
        }

 obtenerfecha(e){
var fecha =moment(e.value);
console.log("fechaoriginal:" + e.value);
console.log("fechaformateda es:" + fecha.format("YYYY/MM/DD"));
}
        
        nombreChange(value){
        
            var str = value; 
                var matches = str.match(/(\d+)/); 
                  
                if (matches) { 
                }else{
                    this.setState({
                        nombre: value
                    });
                }
        }
        apellidoChange(value){
            
            var str = value; 
                var matches = str.match(/(\d+)/); 
                  
                if (matches) { 
                }else{
                    this.setState({
                        apellido: value
                    });
                }
        }
        

    render() {
        const cookies = new Cookies();
        return (
            <>
            <DASH/>
            <div id="contenidogeneral">
            <br/>
                <div className="body3">
                    <div className="containers">
                        <div className="tabla">
                            <div className="flip-card" id="rotar1">
                                <div className="flip-card-inner" id="rotar">
                                    <div className="flip-card-front">
                                        <div className="container" id="stylea" >
                                            <div className="card">
                                                <img src={cookies.get('url_foto')} id="styleb" class="card-img-top" alt="..."></img>
                                                <img src="https://image.flaticon.com/icons/png/512/72/72336.png" className="personalizao" alt="..."></img>
                                                <br/>                                ​​
                                                
                                                        <input className="personalizado" type="file"  onChange={this.fileSelectedHadler}   ></input>
                                                        <br/>
                                                        <button   onClick={this.fileUploadHadler} id="idimout" >Actualizar Foto</button>
                                                        
                                                  
                                                    
                                                <div className="card-body">
                                                    <h4 className="card-title" id="stylef">{cookies.get('nombres')} {cookies.get('apellidos')}</h4>
                                                    <br />
                                                    <h5 id="stylef">{cookies.get('profesion')}</h5>
                                                    
                                                    <center>
                                                        <p className="card-text" id="stylef">{cookies.get('gustos')}</p>
                                                    </center>
                                                    <br />
                                                    <table id="lastajdas" className="table table-bordered ">
                                                        <thead>
                                                            <tr>
                                                                <th scope="col" >TAJADAS</th>
                                                                <th scope="col">{cookies.get('tajadas')}</th>
                                                                
                                                            </tr>
                                                        </thead>
                                                           
                                                    </table>
                                                    <button type="button" class="btn btn-primary" id="stylebt" onClick={() => this.rotamos()} >Actualizar</button>
                                                  
                                                   <button type="button"  data-toggle="modal" data-target="#exampleModal1" class="btn btn-primary" id="stylebt153" >Cambiar Contraseña</button>
                                                  
                                                </div>
                                            </div>

                                        </div>
                                    </div>
                                    <div className="flip-card-back">




                                       <div className="card">
                                             <div className="card-body" id="zindex">
                                                <h4 className="card-title" id="stylec">Actualizar datos</h4>
                                                
                                                    
                                                     
                                                <div className="card-body" >
                                             <form id="s4tyle"  onSubmit={this.actualizarusuario}>
                                        <div className="form-row" >
                                            <div className="form-group col-md-6">
                                                <label for="inputEmail4"><h5>Nombres</h5></label>
                                                <input ref={this.Nombre} type="text" className="form-control" id="inputEmail4"placeholder={cookies.get('nombres')} required></input>
                                            </div>
                                            <div className="form-group col-md-6">
                                                <label for="inputPassword4"><h5>Apellidos</h5></label>
                                                <input ref={this.Apellido} type="text" className="form-control" id="inputEmail4"placeholder={cookies.get('apellidos')} required></input>
                                            </div>
                                        </div>
                                        <div className="form-row">
                                            <div className="form-group col-md-6">
                                                <label for="inputEmail4"><h5>Profesión</h5></label>
                                                <input ref={this.Profesion} type="text" className="form-control" id="inputEmail4"placeholder={cookies.get('profesion')} required></input>
                                            </div>
                                            <div className="form-group col-md-6">
                                                <label for="inputPassword4"><h5>Fecha nacimiento</h5></label>
                                                <input ref={this.Fecha_nacimiento} type="date" className="form-control" id="inputEmail4"placeholder={cookies.get('fecha_nacimiento')} required></input>
                                            </div>
                                        </div>
                                        <div className="form-row">
                                            <div className="form-group col-md-6">
                                                <label for="inputEmail4"><h5>Correo personal</h5></label>
                                                <input ref={this.CORREOP} type="email" className="form-control" id="inputEmail4"placeholder={cookies.get('correo_personal')}></input>
                                            </div>
                                            <div className="form-group col-md-6">
                                                <label for="inputPassword4"><h5>Gustos</h5></label>
                                                <input ref={this.Gustos} type="text" className="form-control" id="inputEmail4"placeholder={cookies.get('gustos')}></input>
                                            </div>
                                        </div>
                                        <div className="form-row">
                                            <div className="form-group col-md-6">
                                                <label for="inputEmail4"><h5>Fecha entrada</h5></label>
                                                <input ref={this.Fecha_entrada} type="date" className="form-control" id="inputEmail4"placeholder={cookies.get('fecha_entrada')}></input>
                                            </div>
                                            <div className="form-group col-md-6">
                                                <label for="inputPassword4"><h5>Fecha salida</h5></label>
                                                <input ref={this.Fecha_salida} type="date" onChange="obtenerfecha(.this)" className="form-control" id="inputEmail4"placeholder={cookies.get('fecha_salida')}></input>
                                            </div>
                                        </div>
                                        <div className="form-row">
                                            <div className="form-group col-md-6">
                                                <label for="inputEmail4"><h5>Correo corporativo</h5></label>
                                                <input ref={this.Correo_corporativo} type="email" className="form-control" id="inputEmail4"placeholder={cookies.get('correo_corporativo')}></input>
                                            </div>
                                            <div className="form-group col-md-6">
                                                <label for="inputPassword4"><h5>Cedula</h5></label>
                                                <input ref={this.ID}  type="text" className="form-control" id="inputEmail4"placeholder={cookies.get('id')}></input>
                                            </div>
                                        </div>
                                        <div className="form-row">
                                            
                                            <div className="form-group col-md-6">
                                                <label for="inputPassword4"><h5>Telefono</h5></label>
                                                <input ref={this.TEL} type="text" className="form-control" id="inputEmail4"placeholder={cookies.get('telefono')}></input>
                                            </div>
                                              {/* <div className="custom-file">
                                                        <input type="file" className="custom-file-input" id="customFileLang" lang="es"></input>
                                                        <label className="custom-file-label" for="customFileLang">Seleccionar foto de perfil</label>
                                                    </div> */}
                                        </div>
                                        
                                    </form>
 
                                                <a onClick={() => this.actualizarusuario()}  className="link3"   >Actualizar</a> 
                                                    
                                                    <a  className="link3" href='/Perfil_Usuario'id="Enviar"> Cancelar</a>
                                                    </div></div>
                                        </div> 
                                    </div>
                                </div>

                            </div>
                        </div>


                    </div>

                </div>
            </div>
            {/* <!-- Modal --> */}
<div class="modal fade" id="exampleModal1" tabindex="-1" role="dialog" aria-labelledby="exampleModalLabel" aria-hidden="true">
  <div class="modal-dialog" role="document">
    <div class="modal-content">
      <div class="modal-header" id="colordeelmodal" >
        <h5 class="modal-title"  id="exampleModalLabel">Cambiar Contraseña</h5>
        <button type="button" class="close" data-dismiss="modal" aria-label="Close">
          <span aria-hidden="true">&times;</span>
        </button>
      </div>
      <div class="modal-body">
      {/* onClick={() => this.actualizarcolor()} */}
      <form   onSubmit={this.actualizarcontrasena}>
                                                    <div className="form-group">
                                                        <label for="formGroupExampleInput" id="styled">
                                                            <h5>Contraseña Actual</h5>
                                                        </label>
                                                        <input ref={this.ContrasenaActual}   type="password" className="form-control" id="formGroupExampleInput" required></input>
                                                            
                                                    </div>
                                                    <div className="form-group">
                                                        <label for="formGroupExampleInput" id="styled">
                                                            <h5>Nueva Contraseña</h5>
                                                        </label>
                                                        <input ref={this.NuevaContrasena} type="password" className="form-control" id="formGroupExampleInput" required></input>
                                                    </div>
                                                    <div className="form-group">
                                                        <label for="formGroupExampleInput" id="styled">
                                                            <h5>Confirmar Contraseña </h5>
                                                        </label>
                                                        <input ref={this.ConfirmarContrasena}  type="password" className="form-control" id="formGroupExampleInput"  required></input>
                                                    </div>
                                                    </form>
                                                    <button type="submit"  onClick={() => this.actualizarcontrasena()}  className="link3"   >Actualizar</button>
                                                    

                                                
                                          
      </div>
      <div class="modal-footer">
        <button type="button" class="btn btn-secondary" data-dismiss="modal">Close</button>
      </div>
    </div>
  </div>
</div>

            </>
        );
    }
}
export default PERFILU;