import React from "react";
import "../css/login.css";
import sweet from "sweetalert";
import Cookies from 'universal-cookie';

class LOGIN extends React.Component {
    state = {
        res: []
    };
    Correo = React.createRef();
  Contrasena = React.createRef();
   
       

        iniciosesion = (e) => {
          e.preventDefault();
          
        const correo = this.Correo.current.value;
            const contrasena = this.Contrasena.current.value;
            const cookies = new Cookies();
        
            fetch(`http://semillerows3/PlataformaSemilleroBackend/api/usuarios/Correo?Correo=${correo}`)
          // fetch(`http://172.35.2.124:3002/api/usuarios/Correo?Correo=${correo}`)
            
          // fetch(`http://localhost:8000/api/usuarios/Correo?Correo=${correo}`)
            .then((response) => response.json())
            .then((res) => {
              if (res[0]) {
                this.state = res[0];
                if(contrasena==this.state.contrasena){
                  // constantes de las cookies  
                  const id=this.state.id_usuario;
                  const nombres=this.state.nombres;
                  const apellidos=this.state.apellidos;
                  const fecha_nacimiento=this.state.fecha_nacimiento;
                  const telefono=this.state.telefono;
                  const gustos=this.state.gustos;
                  const profesion=this.state.profesion;
                  const correo_corporativo=this.state.correo_corporativo;
                  const contrasena=this.state.contrasena;
                  const fecha_salida=this.state.fecha_salida;
                  const fecha_entrada=this.state.fecha_entrada;
                  const id_rol=this.state.id_rol;
                  const id_estado=this.state.id_estado;
                  const id_horario=this.state.id_horario;
                  const correo_personal=this.state.correo_personal;
                  const hdv=this.state.hdv;
                  const foto=this.state.foto;
                  const url_hdv=this.state.url_hdv;
                  const url_foto=this.state.url_foto;
                  const color_dash=this.state.color_dash; 
                  const tajadas=this.state.tajadas;
                // creamos las cookies y asignamos tiempo de caducidad 
                  // cookies.set("id",`${id}`, {path: '/ ; max-age=120*120'} );
                  cookies.set("id",`${id}`, {path: '/ ; '} );
                  cookies.set("nombres",`${nombres}`, {path: '/ ; '} );
                  cookies.set("apellidos",`${apellidos}`, {path: '/ ; '} );
                  cookies.set("fecha_nacimiento",`${fecha_nacimiento}`, {path: '/ ; '} );
                  cookies.set("telefono",`${telefono}`, {path: '/ ; '} );
                  cookies.set("gustos",`${gustos}`, {path: '/ ; '} );
                  cookies.set("profesion",`${profesion}`, {path: '/ ; '} );
                  cookies.set("correo_corporativo",`${correo_corporativo}`, {path: '/ ; '} );
                  cookies.set("contrasena",`${contrasena}`, {path: '/ ; '} );
                  cookies.set("fecha_salida",`${fecha_salida}`, {path: '/ ; '} );
                  cookies.set("fecha_entrada",`${fecha_entrada}`, {path: '/ ; '} );
                  cookies.set("id_rol",`${id_rol}`, {path: '/ ; '} );
                  cookies.set("id_estado",`${id_estado}`, {path: '/ ; '} );
                  cookies.set("id_horario",`${id_horario}`, {path: '/ ; '} );
                  cookies.set("correo_personal",`${correo_personal}`, {path: '/ ; '} );
                  cookies.set("hdv",`${hdv}`, {path: '/ ; '} );
                  cookies.set("foto",`${foto}`, {path: '/ ; '} );
                  cookies.set("url_hdv",`${url_hdv}`, {path: '/ ; '} );
                  cookies.set("url_foto",`${url_foto}`, {path: '/ ; '} );
                  cookies.set("color_dash",`${color_dash}`, {path: '/ ; '} );
                  cookies.set("tajadas",`${tajadas}`, {path: '/ ; '} );
                  // redireccionamos al principal 
                  window.location.href="/Principal"; 
                } else {
                  sweet({
                    text:` ${this.state.nombres}, su contraseña es incorrecta vuelva a intentarlo`,
                    icon: "error",
                    timer: "3000",
                    button: false
                  });
                }
               
              } else {
                          sweet({
                            text: "Usuario no encontrado vuelva a intentarlo",
                            icon: "error",
                            timer: "3000",
                            button: false
                          });
                        }
                      
                      })
                      .catch((err) => console.log(err));
                  
        }
    render() {
        return (
            <>
                <div id="mainPrincipal">
                    <div className="opacidad">
                        <div className="session" id="session">
                            <div className="contentLogin">
                                <div className="imagenla"></div>
                                <div className="titleLogin12">
                                    <h1 className="titleLogin12" >Iniciar Sesión</h1>
                                </div>


                                <form  onSubmit={this.iniciosesion}>
                                <input ref={this.Correo}  className="input" type="email" id="correo" name="correo" placeholder="Ingrese Correo Electrónico"  required/>
                                   
                                    <br />
                                    <input ref={this.Contrasena} className="input" type="password" name="contrasena" id="contrasena" placeholder="Ingrese Contraseña"  required/>
                                    <br />
                                    <br />
                                    <div class="contentButtons">
                                        <button type="button" class="link3"><a className="hiper" href='/index' id="Enviar"> Cancelar</a></button>
                                        <button type="submit" className="link3" id="Enviar"> Ingresar</button>
                                    </div>
                                </form>
                            </div>
                        </div>
                    </div>

                </div>
            </>
        );
    }
}
export default LOGIN;
