import React from "react";
import "../css/Primeros_Pasos.css";
import  DASH from "./Dashboard"
import NAV from "./nav";
import Cookies from 'universal-cookie';

class PRIMEROS extends React.Component {
    state = {
        res: []
    };
    componentDidMount(){
        const cookies = new Cookies();
    if(cookies.get('color_dash') =="azul" || cookies.get('color_otro') =="azul"  ){
        document.getElementById("descripcioncolor1").classList.add("colordescripcion"); 
        document.getElementById("descripcioncolor2").classList.add("colordescripcion"); 
        document.getElementById("descripcioncolor3").classList.add("colordescripcion"); 
        document.getElementById("descripcioncolor4").classList.add("colordescripcion"); 
        document.getElementById("cambiarelbanner").classList.add("banner11"); 
}else if(cookies.get('color_dash') =="morado" || cookies.get('color_otro') =="morado"  ){
    document.getElementById("descripcioncolor1").classList.add("colordescripcion2"); 
    document.getElementById("descripcioncolor2").classList.add("colordescripcion2"); 
    document.getElementById("descripcioncolor3").classList.add("colordescripcion2"); 
    document.getElementById("descripcioncolor4").classList.add("colordescripcion2"); 
    document.getElementById("cambiarelbanner").classList.add("banner2"); 
}else if(cookies.get('color_dash') =="verde" || cookies.get('color_otro') =="verde"  ){
    document.getElementById("descripcioncolor1").classList.add("colordescripcion3"); 
    document.getElementById("descripcioncolor2").classList.add("colordescripcion3"); 
    document.getElementById("descripcioncolor3").classList.add("colordescripcion3"); 
    document.getElementById("descripcioncolor4").classList.add("colordescripcion3"); 
    document.getElementById("cambiarelbanner").classList.add("banner3"); 
}else if(cookies.get('color_dash') =="gris" || cookies.get('color_otro') =="gris"  ){
    document.getElementById("descripcioncolor1").classList.add("colordescripcion4"); 
    document.getElementById("descripcioncolor2").classList.add("colordescripcion4"); 
    document.getElementById("descripcioncolor3").classList.add("colordescripcion4"); 
    document.getElementById("descripcioncolor4").classList.add("colordescripcion4"); 
    document.getElementById("cambiarelbanner").classList.add("banner4"); 
}
}

    render() {
        return (
            <>
            <DASH/>
            <div id="contenidogeneral">
                <div id="body">
                    <div className="banner1 justify-content-md-center" id="cambiarelbanner">
                        <br />
                    </div>
                    <br />

                    {/* <!--Tareas por hacer--> */}
                    <div id="au" className="separator">&nbsp;&nbsp;Bienvenidos al Semillero &nbsp;&nbsp;</div>
                    <br />
                    {/* <!-- tarjetas --> */}
                    <div className="blog-card alt">
                        <div className="meta">
                            <div id="style1" className="photo">
                            </div>
                             <ul className="details">
                                <li className="tags">

                                    <li><a href="https://esri.co/">Esri Colombia </a></li>
                                    <li><a href="#">Esri Ecuador</a></li>
                                    <li><a href="#">Esri Panama</a></li>
                                    <li><a href="#">Esri INC</a></li>

                                </li>
                            </ul>
                        </div>
                        <div className="description" id="descripcioncolor1">
                            <h1>¿Qué es ESRI?</h1>
                            <p>Somos los creadores de ArcGIS, un potente software de mapeo y análisis</p>

                        </div>
                    </div>
                    <br /><br />
                    <div className="blog-card">
                        <div className="meta">
                            <div id="style2" className="photo">
                            </div>

                            <ul className="details">

                                <li className="tags">

                                    <li><a target="_blank" href="#">ArcGIS Online </a></li>
                                    <li><a href="#">Esri Training</a></li>
                                    <li><a href="#">Okta</a></li>
                                    <li><a href="#">Outlook</a></li>
                                </li>

                            </ul>
                        </div>
                        <div className="description"  id="descripcioncolor2">
                            <h1>Crear cuentas</h1>
                            <p>Es necesario adquirir las credenciales para poder acceder a los recursos de ArcGIS y Okta los cuáles se usarán en el tiempo que pertenezcan al Semillero</p>
                        </div>
                    </div>
                    <br /><br />
                    <div className="blog-card alt">
                        <div className="meta">
                            <div id="style3" className="photo"></div>
                               <ul className="details">
                                    <li className="tags">

                                        <li><a href="#">Cursos</a></li>
                                        <li><a href="#">Tutoriales</a></li>

                                    </li>
                                </ul>
                            </div>
                            <div className="description"  id="descripcioncolor3">
                                <h1>Entrenamiento</h1>
                                <p>La compañía cuenta con una plataforma de aprendizaje en la cuál encontramos cursos de conocimiento
                                            sobre las apps, páginas de la empresa y lenguajes de progamación de interés</p>

                            </div>
                        </div>
                        <br /><br /><br /><br />
                        <div className="blog-card">
                            <div className="meta">
                                <div id="style4" className="photo">
                                </div>
                                <ul className="details">

                                    <li className="tags">

                                        <li><a href="#">Llegar tarde</a></li>
                                        <li><a href="#">Perder Kahoot</a></li>
                                        <li><a href="#">Decir groserias</a></li>
                                        <li><a href="#">Dejar PC desbloqueado</a></li>
                                    </li>

                                </ul>
                            </div>
                            <div className="description"  id="descripcioncolor4">
                                <h1>¿Qué son tajadas?</h1>
                                <p>Es un pequeño juego del Semillero el cual consiste en que si se llega tarde a una reunión o decimos alguna
                                                groseria se nos cobra 5.000 pesos, los cuáles se utilizan para una actividad de socialización con los compañeros del Semillero</p>
                            </div>
                        </div><br /><br />


                    </div>
            </div>
            </>
        );
    }
}
export default PRIMEROS;
