import React, { Component } from 'react'
import './App.css';
import { BrowserRouter as Router, Switch, Route } from 'react-router-dom'

import CONSULTARU from './components/js/Consulta_Usuario'
import DASH from './components/js/Dashboard'
import NAV from './components/js/nav'
import FORO from './components/js/Foro'
import INDEX from './components/js/index'
import LOGIN from './components/js/login'
import PERFILU from './components/js/Perfil_Usuario'
import PRIMEROS from './components/js/Primeros_Pasos'
import PRINCIPAL from './components/js/Principal'
import REGISTRARU from './components/js/Registrar_Usuario'
import ContainerGestionProyectos from './Componentes2/Gestion_Proyectos'
import GestionProyectos from './Componentes2/nuevo/Proyecto'
import Container from './Componentes2/mis_proyectos';
import CONSULTARUACTIVO from './components/js/Consulta_Usuarios_Activos'
import CONSULTARUINACTIVOS from './components/js/Consulta_Usuarios_Inactivos'
import CONSULTATODOS from './components/js/Consultar_Todos_Proyectos'

function App() {
  return (
    
    <Router >
      <Switch>
        <Route path="/AplicacionSemilleroFrontend/PlataformaSemillero/Semillero/Consulta_Inactivos" exact component={CONSULTARUINACTIVOS} />
        <Route path="/AplicacionSemilleroFrontend/PlataformaSemillero/Semillero/Consulta_Usuario" exact component={CONSULTARUACTIVO} />
        <Route path="/AplicacionSemilleroFrontend/PlataformaSemillero/Semillero/Consulta_Activos" exact component={CONSULTARU} />
        <Route path="/AplicacionSemilleroFrontend/PlataformaSemillero/Semillero/Dashboard" exact component={DASH} />
        <Route path="/AplicacionSemilleroFrontend/PlataformaSemillero/Semillero/Nav" exact component={NAV} /> 
        <Route path="/AplicacionSemilleroFrontend/PlataformaSemillero/Semillero/Foro" exact component={FORO} />
        <Route path="/AplicacionSemilleroFrontend/PlataformaSemillero/Semillero/" component={INDEX} />
        <Route path="/AplicacionSemilleroFrontend/PlataformaSemillero/Semillero/login" exact component={LOGIN} />
        <Route path="/AplicacionSemilleroFrontend/PlataformaSemillero/Semillero/Perfil_Usuario" exact component={PERFILU} />
        <Route path="/AplicacionSemilleroFrontend/PlataformaSemillero/Semillero/Primeros_Pasos" exact component={PRIMEROS} />
        <Route path="/AplicacionSemilleroFrontend/PlataformaSemillero/Semillero/Principal" exact component={PRINCIPAL} />
        <Route path="/AplicacionSemilleroFrontend/PlataformaSemillero/Semillero/Registrar_Usuario" exact component={REGISTRARU} /> 
        <Route path="/AplicacionSemilleroFrontend/PlataformaSemillero/Semillero/Consulta_De_Proyectos" exact component={ContainerGestionProyectos}></Route>
        <Route path="/AplicacionSemilleroFrontend/PlataformaSemillero/Semillero/Gestion_De_Proyectos" exact component={Container}></Route>
        <Route path="/AplicacionSemilleroFrontend/PlataformaSemillero/Semillero/Gestion_Proyectos" exact component={GestionProyectos}></Route>
        <Route path="/AplicacionSemilleroFrontend/PlataformaSemillero/Semillero/Consulta_Todos" exact component={CONSULTATODOS}></Route>
      
      </Switch>
    </Router>
  );
}


export default App;