// funcion para eliminar las cookis y cerrar la sesion 
delitcook(){
    const  cookies = new Cookies();
    const id_usuario= cookies.get('id');
    const nombres= cookies.get('nombres');
    const apellidos= cookies.get('apellidos');
    const fecha_nacimiento= cookies.get('fecha_nacimiento');
    const numero= cookies.get('numero');
    const gustos= cookies.get('gustos');
    const profesion= cookies.get('profesion');
    const correo_corporativo= cookies.get('correo_corporativo');
    const contrasena= cookies.get('contrasena');;
    const fecha_salida= cookies.get('fecha_salida');
    const fecha_entrada= cookies.get('fecha_entrada');
    const id_rol= cookies.get('id_rol');
    const id_estado= cookies.get('id_estado');
    const id_horario= cookies.get('id_horario');
    const correo_personal=cookies.get('correo_personal');
    const hdv= cookies.get('hdv');
    const foto= cookies.get('foto');
    const url_hdv= cookies.get('url_hdv');
    const url_foto= cookies.get('url_foto');
    const color_dash= cookies.get('color_dash');
    const tajadas= cookies.get('tajadas');

    cookies.remove("id_usuario",`${id_usuario}`,{path: '/ ; '});
    cookies.remove("nombres",`${nombres}`,{path: '/ ; '});
    cookies.remove("apellidos",`${apellidos}`,{path: '/ ;'});
    cookies.remove("fecha_nacimiento",`${fecha_nacimiento}`,{path: '/ ;'});
    cookies.remove("numero",`${numero}`,{path: '/ ; '});
    cookies.remove("gustos",`${gustos}`,{path: '/ ; '});
    cookies.remove("profesion",`${profesion}`,{path: '/ ; '});
    cookies.remove("correo_corporativo",`${correo_corporativo}`,{path: '/ ; '});
    cookies.remove("contrasena",`${contrasena}`,{path: '/ ; '});
    cookies.remove("fecha_salida",`${fecha_salida}`,{path: '/ ; '});
    cookies.remove("fecha_entrada",`${fecha_entrada}`,{path: '/ ; '});
    cookies.remove("id_rol",`${id_rol}`,{path: '/ ; '});
    cookies.remove("id_estado",`${id_estado}`,{path: '/ ; '});
    cookies.remove("id_horario",`${id_horario}`,{path: '/ ; '});
    cookies.remove("correo_personal",`${correo_personal}`,{path: '/ ; '});
    cookies.remove("hdv",`${hdv}`,{path: '/ ; '});
    cookies.remove("foto",`${foto}`,{path: '/ ; '});
    cookies.remove("url_hdv",`${url_hdv}`,{path: '/ ; '}); 
    cookies.remove("url_foto",`${url_foto}`,{path: '/ ; '});
    cookies.remove("color_dash",`${color_dash}`,{path: '/ ; '});
    cookies.remove("tajadas",`${tajadas}`,{path: '/ ; '});

}


    bosy= (e) => {
        e.preventDefault();
        try{
          this.props.history.push({
            pathname: '/Perfil_Usuario',
            state: { detail: this.state,}
          })}
          catch{
            this.props.history.push({
                pathname: '/Principal',
                state: { detail: this.state, }
              })
          
        }
         
      };
      
